import { Component } from '@angular/core';
import { GOS_2017 } from '../core/gos-data/2017';
import { GOS_2018 } from '../core/gos-data/2018';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  gosData2017 = GOS_2017;
  gosData2018 = GOS_2018;
  constructor() {

  }

  getPercent(data2017, data2018): string {
    const num2017 = parseInt(data2017.replace(new RegExp(' ', 'g'), ''));
    const num2018 = parseInt(data2018.replace(new RegExp(' ', 'g'), ''));
    return ((num2017 / num2018) * 100 - 100).toFixed(1);
  }
}
